


















































































































































































.portfolio {
  padding: 24px 0;
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
  grid-auto-rows: 1fr;
  grid-gap: 24px;
}

.portfolio::before {
  content: '';
  width: 0;
  padding-bottom: 100%;
  grid-row: 1 / 1;
  grid-column: 1 / 1;
}

@media (max-width: 969px) {
  .portfolio {
    grid-template-columns: 1fr 1fr;
    padding: 0;
    padding-bottom: 64px;
  }
}

@media (max-width: 480px){
  .portfolio {
    grid-template-columns: 1fr;
    padding: 0;
    padding-bottom: 64px;
  }
}
